.news-page__wrapper {
  @include wrapper;
  padding-top: 55px;
  padding-bottom: 90px;
}

.news-page__title {
  @include main-title;
  grid-column: 1/-1;
  margin-bottom: 53px;
}

.news-page__head-list {
  @include list-reset;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 50px;

  @media (min-width: 960px) {
    grid-template-columns: repeat(4, 1fr);
    gap: 26px;
    margin-bottom: 80px;
  }
}

.news-page__head-list-item {
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: #b65a23;

  a {
    color: inherit;
    text-decoration: underline;
  }

  @media ($tablet) {
    font-size: 18px;
    line-height: 30px;
  }

  p {
    margin: 0;
    margin-top: 18px;
    text-transform: uppercase;
  }

  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}

.news-page__content {
  @media (min-width: 960px) {
    display: grid;
    grid-template-columns: 0.9fr 2.85fr;
    column-gap: 26px;
  }
}

.news-page__news-list {
  @include list-reset;
  display: grid;
  row-gap: 50px;

  @media ($tablet) {
    grid-column: 2/3;
    row-gap: 100px;
  }
}

.news-page__news-article {
  @media ($tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 26px;
    place-items: flex-start;
    align-items: flex-start;
  }
  @media ($desktop) {
    grid-template-columns: 1fr 2fr;
    column-gap: 40px;
  }
}

.news-page__pic-link {
  @media ($tablet) {
    grid-row: 1/3;
  }

  @media ($desktop) {
    padding-top: 5px;
  }
}

.news-page__article-info {
  @media ($tablet) {
    grid-row: 1/2;
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 42px;
    align-items: center;
  }
}

.news-page__article-type {
  grid-template-columns: 1/2;
  grid-row: 2/3;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #9a99a2;
  text-transform: uppercase;
}

.news-page__article-date {
  grid-template-columns: 1/2;
  grid-row: 1/2;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  /* or 133% */

  color: #4b4747;
}

.news-page__article-btn {
  grid-template-columns: 2/3;
  grid-row: 1/3;
  font-weight: 700;
  font-size: 15px;
  line-height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 140px;
  color: #ffffff;
  background: #e7a45e;
  border-radius: 3px;
}

.news-page__article-lead {
  @media ($tablet) {
    grid-row: 2/3;
  }
}

.news-page__article-lead {
  h2 {
    margin: 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    color: #b65a23;
    text-transform: uppercase;

    a {
      color: inherit;
      text-decoration: underline;
    }
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
  }
}

.news-page__sidebar {
  display: grid;
  place-content: flex-end;
  row-gap: 50px;
  grid-row: 1/4;

  @media ($desktop) {
    row-gap: 100px;
  }
}

.news-page__list-filter {
  margin-bottom: 45px;

  @media ($tablet) {
    grid-column: 2/3;
    row-gap: 100px;
    margin-bottom: 85px;
  }
}
